/********box css************/
.plp-wrapper {
    background: #f6f6f6;
    border-bottom: solid 2px #d8dadb;
    margin-bottom: 30px;
    min-height: 500px;
    overflow: hidden;
}

.plp-wrapper .cta-holder {
    position: relative;
    text-align: center;
}
.plp-wrapper .cta-holder figure {
    background-color: #1f2222;margin: 0;
}

.plp-wrapper .cta-holder .cta {
    background-color: #1f2222;
    height: 100%;
    opacity: 0;
    width: 100%;
    position: absolute;
    top: -100%;
    left: 0;
    transition: all .25s ease-in-out;
}
.plp-wrapper .cta-holder .cta span {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    display: block;
    left: 0;
    margin: 0 auto;
    text-align: center;
    right: 0;
}

.btn.btn-ghost {
    background-color: transparent;
    border: solid 2px #fff;
     color: #fff;
    padding-bottom: 10px;
    padding-top: 10px;
}
.btn.btn-ghost:hover{
    border-color: #004dda;
    color: #004dda;
}
.plp-wrapper .content-holder {
    padding: 38px 35px;
}
.plp-wrapper .content-holder p {
    color: #7b8b8e;
    font-size: 14px;
    line-height: 1.714285714285714;
    margin-bottom: 26px;
    display: block;
    display: -webkit-box;
    height: 72px;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
}
.plp-wrapper .content-holder .period {
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
}
.plp-wrapper .content-holder h3 {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.333333333333333;
    margin-bottom: 26px;
}
.plp-wrapper:hover .cta-holder .cta {
    background-color: rgba(31,34,34,.9);
    opacity: 1;
    top: 0;
}
/***end box-css******/
